import { useRouteError } from 'react-router-dom';

const ErrorPage = () => {
  const error = useRouteError();
  let message = '    .';
  console.log(error);
  if (error.status === 500) {
    message = error.data;
  }

  if (error.status === 404) {
    message = error.data;
  }
  return (
    <>
      <h1>Oops</h1>
      <p>{message}</p>
    </>
  );
};

export default ErrorPage;